<template>
  <div id="loading-container" class="vs-con-loading__container">
    <component :is="`LgIssued${dataLayout}`" :key="Date.now()" :parent="this"/>
  </div>
</template>

<script>
import LgIssuedListLayout from '@/views/trade/lg/lg-issued/lg-issued-browse/LgIssuedListLayout'
import LgIssuedCardLayout from '@/views/trade/lg/lg-issued/lg-issued-browse/LgIssuedCardLayout'
import LgIssuedPivotLayout from '@/views/trade/lg/lg-issued/lg-issued-browse/LgIssuedPivotLayout'
import LgIssuedTableMixin from '@/mixins/trade/lg/LgIssuedTableMixin'

export default {
  name: 'lg-issued-browse',
  mixins: [LgIssuedTableMixin],
  components: {
    LgIssuedListLayout,
    LgIssuedCardLayout,
    LgIssuedPivotLayout
  },
  data: function () {
    return {
      dataLayout: 'ListLayout'
    }
  },
  mounted: function () {
    let date = new Date()
    this.fromDate = date.setDate(date.getDate() - 30)
    this.loadLgTableData()
  },
  created: function () {
    this.$eventHub.$on('toolClicked', (toolId) => { this.dataLayout = toolId })
  },
  beforeDestroy: function () {
    this.$eventHub.$off('toolClicked', (toolId) => { this.dataLayout = toolId })
  }
}
</script>
