<template>
  <nv-card>
    <div class="inline-form p-4 mb-base" v-if="findFormVisible">
      <div class="nv-row mb-4">
        <div class="nv-col sm:w-1/2 w-full required">
          <label class="vs-input--label">{{ $t('FROM_DATE') }}</label>
          <nv-input-group>
            <template slot="append">
              <div class="append-text bg-dark">
                <i class="far fa-calendar-alt"></i>
              </div>
            </template>
            <datepicker class="w-full" :placeholder="$t('SELECT_DATE')" v-model="parent.fromDate" :format="parent.dateFormat"></datepicker>
          </nv-input-group>
        </div>
        <div class="nv-col sm:w-1/2 w-full required">
          <label class="vs-input--label">{{ $t('TO_DATE') }}</label>
          <nv-input-group>
            <template slot="append">
              <div class="append-text bg-dark">
                <i class="far fa-calendar-alt"></i>
              </div>
            </template>
            <datepicker class="w-full" :placeholder="$t('SELECT_DATE')" v-model="parent.toDate" :format="parent.dateFormat"></datepicker>
          </nv-input-group>
        </div>
      </div>
      <div class="nv-row">
        <div class="nv-col w-full">
          <div class="flex justify-between">
            <vs-dropdown vs-trigger-click>
              <a class="flex items-center" href="#">
                {{ $t('CHOOSE_INTERVAL') }}
                <i class="far fa-chevron-down ml-2"></i>
              </a>
              <vs-dropdown-menu>
                <vs-dropdown-item @click="parent.chooseInterval('day')">
                  {{ $t('PAST_DAY') }}
                </vs-dropdown-item>
                <vs-dropdown-item @click="parent.chooseInterval('week')">
                  {{ $t('PAST_WEEK') }}
                </vs-dropdown-item>
                <vs-dropdown-item @click="parent.chooseInterval('month')">
                  {{ $t('PAST_MONTH') }}
                </vs-dropdown-item>
                <vs-dropdown-item @click="parent.chooseInterval('year')">
                  {{ $t('PAST_YEAR') }}
                </vs-dropdown-item>
              </vs-dropdown-menu>
            </vs-dropdown>
            <div class="flex justify-right">
              <vs-button class="px-5 py-3 mr-2" @click="submitFind" color="primary" type="filled">{{ $t('SUBMIT') }}</vs-button>
              <vs-button class="px-5 py-3 mr-2" @click="findFormVisible = false" color="primary" type="border">{{ $t('CANCEL') }}</vs-button>
            </div>
          </div>
        </div>
      </div>
    </div>
    <vs-table :data="parent.lgTableData" :noDataText="parent.lgTableNoDataText" search>
      <div slot="header" class="flex flex-wrap-reverse items-center flex-grow justify-between">
        <div class="flex flex-wrap-reverse items-center">
          <vs-button class="p-3 mr-4" v-if="parent.lgTableData.length > 0" @click="parent.exportXls(parent.lgTableData, parent.columnDefs)" type="filled" icon="fa-file-excel" icon-pack="fal">{{ $t('EXPORT_XLS') }}</vs-button>
          <vs-button class="p-3 mr-4" v-if="parent.lgTableData.length > 0" @click="parent.exportCsv(parent.lgTableData, parent.columnDefs)" type="filled" icon="fa-file-csv" icon-pack="fal">{{ $t('EXPORT_CSV') }}</vs-button>
          <vs-button class="p-3 mr-4" @click="showFindForm" type="filled" icon="fa-binoculars" icon-pack="far" :disabled="parent.lgTableBusy">{{ $t('FIND') }}</vs-button>
          <vs-button class="p-3 mr-4" @click="parent.refreshLgTable" type="filled" icon="fa-sync" icon-pack="far" :disabled="parent.lgTableBusy">{{ $t('REFRESH') }}</vs-button>
        </div>

        <div class="flex justify-end mr-4">
          <div class="p-2 border border-solid border-grey-light rounded-full d-theme-dark-bg flex items-center justify-between font-medium">
            <span class="mx-2">{{ parent.lgTablePagingInfo }}</span>
          </div>
        </div>
      </div>

      <template slot="thead">
        <vs-th sort-key="lgNumber">
          {{ this.$t('LG_REFERENCE_NUMBER') }}
        </vs-th>
        <vs-th sort-key="typeName">
          {{ this.$t('LG_TYPE') }}
        </vs-th>
        <vs-th sort-key="beneficiaryName">
          {{ this.$t('BENEFICIARY_NAME') }}
        </vs-th>
        <vs-th sort-key="issuanceDate">
          {{ this.$t('ISSUANCE_DATE') }}
        </vs-th>
        <vs-th sort-key="currencyAbbreviation">
          {{ this.$t('CURRENCY') }}
        </vs-th>
        <vs-th sort-key="lgAmount">
          {{ this.$t('LG_AMOUNT') }}
        </vs-th>
      </template>

      <template slot-scope="{data}">
        <vs-tr :key="index" v-for="(tr, index) in data" :data="tr">
          <vs-td :data="data[index].lgNumber">
            <router-link :to="{ name: 'ViewIssuedLg', params: { id: data[index].lgNumber, fromDate: parent.fromDate, toDate: parent.toDate } }">{{ data[index].lgNumber }}</router-link>
          </vs-td>
          <vs-td :data="data[index].typeName">
            {{ data[index].typeName | placeholder('-') }}
          </vs-td>
          <vs-td :data="data[index].beneficiaryName">
            {{ data[index].beneficiaryName | placeholder('-') }}
          </vs-td>
          <vs-td :data="data[index].issuanceDate">
            {{ data[index].issuanceDate | formatUnixDate }}
          </vs-td>
          <vs-td :data="data[index].currencyAbbreviation">
            {{ data[index].currencyAbbreviation | placeholder('-') }}
          </vs-td>
          <vs-td :data="data[index].lgAmount">
            {{ data[index].lgAmount | currency('') | placeholder('-') }}
          </vs-td>
        </vs-tr>
      </template>
    </vs-table>
  </nv-card>
</template>

<script>
import Datepicker from 'vuejs-datepicker'

export default {
  name: 'lg-issued-list-layout',
  data: function () {
    return {
      findFormVisible: false
    }
  },
  components: {
    Datepicker
  },
  props: {
    parent: {
      type: Object,
      required: true
    }
  },
  methods: {
    showFindForm () {
      this.findFormVisible = true
    },
    submitFind () {
      this.parent.loadLgTableData()
      this.findFormVisible = false
    }
  }
}
</script>
